.loginContainer {
    width: 100vw;
    height: 100vh;
}

.loginCardContainer {
    height: fit-content;
    width: 330px;
    padding: 20px;
    background-color: white;
    margin: auto;
    text-align: center;
    -webkit-box-shadow: 0px 0px 100px 0px rgba(0, 0, 0, 0.2);
    -moz-box-shadow: 0px 0px 100px 0px rgba(0, 0, 0, 0.2);
    box-shadow: 0px 0px 100px 0px rgba(0, 0, 0, 0.2);
}

.loginCardContainer h1 {
    font-size: 24px;
}

.loginCardContainer h2 {
    font-size: 14px;
    font-weight: normal;
    color: var(--contrast);
    margin: 0px;
    cursor: pointer;
}

.loginCardContainer input {
    font-size: 16px;
    padding: 10px;
    margin: 10px;
    width: calc(100% - 50px);
}

.loginCardContainer button {
    border: none;
    padding: 10px;
    margin-top: 50px;
    width: 100%;
    height: 36px;
    border-radius: 18px;
    color: white;
    background-color: var(--primary);
}
